<template>
  <div class="card-body p-3">
    <router-link
      :to="{ name: 'Page', params: { id: page.slug } }"
      role="button"
    >
      <div class="media">
        <img
          :src="
            page.image
              ? page.image
              : `https://res.cloudinary.com/dbszizqh4/image/upload/v1562473096/logo_default.png`
          "
          class="mr-3 rounded-circle img-avatar"
          :alt="page.name"
        />
        <div class="media-body text-muted">
          <h5 class="my-0 h6 font-weight-bold text-dark">
            {{ page.name }}
          </h5>
          <span class="text-dark">@{{ page.slug }}</span
          ><br /><i class="fas fa-globe-americas"></i> {{ status }} |&nbsp;
          <i class="far fa-clock"></i>
          {{ createdAt }}
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";
import { db } from "@/main";

export default {
  name: "OwnerPost",
  props: {
    dataPage: String,
    status: String,
    createdAt: Number
  },
  data() {
    return {
      page: ""
    };
  },
  created() {
    this.getPage();
  },
  methods: {
    async getPage() {
      await axios.get(`${db}/pages/${this.dataPage}/id`).then(response => {
        this.page = response.data;
      });
    }
  }
};
</script>

<style scoped>
.img-avatar {
  width: 3rem;
  height: 3rem;
}
</style>
