<template>
  <li class="media mb-3">
    <router-link
      v-if="user.displayName"
      :to="{
        name: 'Follower',
        params: { id: user.uid }
      }"
      class="text-dark font-weight-bold"
      ><img
        :src="
          user.photoURL
            ? photoURL
            : `https://res.cloudinary.com/dbszizqh4/image/upload/v1592198427/images_lvwix2.png`
        "
        class="mr-3 rounded-circle img-avatar-boton"
        alt="Logo"
    /></router-link>
    <img
      v-else
      src="https://res.cloudinary.com/dbszizqh4/image/upload/v1592198427/images_lvwix2.png"
      class="mr-3 rounded-circle img-avatar-boton"
      alt="Logo"
    />
    <div class="media-body">
      <h5 class="mt-0 mb-1 h6 d-flex justify-content-between">
        <router-link
          v-if="user.displayName"
          :to="{
            name: 'Follower',
            params: { id: user.uid }
          }"
          class="text-dark font-weight-bold"
          >@{{ user.displayName }}</router-link
        >
        <span class="text-dark font-weight-bold" v-else>Anónimo</span>
        <span class="text-muted small">{{ cCreatedAt }}</span>
      </h5>
      {{ cComment }}
    </div>
    <pre class="container" hidden>{{ $data }}</pre>
  </li>
</template>

<script>
import axios from "axios";
import { db } from "@/main";

export default {
  name: "Comment",
  props: {
    cId: String,
    cComment: String,
    cCreatedAt: Number,
    cUserId: String
  },
  data() {
    return {
      user: ""
    };
  },
  created() {
    this.getUser();
  },
  methods: {
    async getUser() {
      await axios.get(`${db}/users/${this.cUserId}/id`).then(response => {
        this.user = response.data;
      });
    }
  }
};
</script>

<style scoped>
.img-avatar-boton {
  width: 2rem;
  height: 2rem;
}
</style>
