<template>
  <div class="post card border-0 shadow-sm mb-4">
    <div
      class="compartido px-3 py-2 border-bottom d-flex justify-content-between"
      hidden
    >
      <h5 class="mt-0 h6" v-if="share">
        Compartido de
        <router-link :to="{ name: 'Page', params: { id: 'ollorente' } }"
          >@ollorente</router-link
        >
      </h5>
      <router-link
        :to="{
          name: 'Post',
          params: { id: page, post: _id }
        }"
        role="button"
        ><i class="fas fa-eye"></i
      ></router-link>
    </div>
    <OwnerPost :dataPage="page" :status="status" :createdAt="createdAt" />
    <img
      v-if="image"
      :src="
        image
          ? image
          : `https://res.cloudinary.com/dbszizqh4/image/upload/v1562472720/default.jpg`
      "
      class="card-img-top"
      alt="photo"
    />
    <div class="embed-responsive embed-responsive-16by9" v-if="video">
      <iframe
        class="embed-responsive-item"
        :src="`https://www.youtube.com/embed/${video}?rel=0`"
        allowfullscreen
      ></iframe>
    </div>
    <div class="card-body p-3">
      <p class="card-text" v-if="content">
        {{ content }}
      </p>
      <div class="btn-group btn-block" role="group" aria-label="Basic example">
        <div class="btn btn-link text-dark">
          <i class="far fa-eye"></i> {{ views }}
        </div>
        <a class="btn btn-link text-dark" href="#" role="button"
          ><i class="fas fa-heart"></i> 15</a
        >
        <a class="btn btn-link text-dark" href="#" role="button"
          ><i class="far fa-comments"></i> 10</a
        >
      </div>
    </div>
    <div class="card-footer p-3">
      <form action="#" v-if="loggIn">
        <div class="input-group">
          <input
            type="text"
            class="form-control"
            placeholder="Recipient's username"
            aria-label="Recipient's username"
            aria-describedby="button-addon2"
          />
          <div class="input-group-append">
            <button class="btn py-0" type="button" id="button-addon2">
              <img
                src="https://res.cloudinary.com/dbszizqh4/image/upload/v1562473096/logo_default.png"
                class="mr-0 rounded-circle img-avatar-boton"
                alt="Logo"
              />
            </button>
          </div>
        </div>
      </form>
      <ul class="list-unstyled mt-4 pt-2" v-if="comments.length > 0">
        <Comment
          v-for="(comment, index) in comments"
          :key="index"
          :cId="comment._id"
          :cComment="comment.comment"
          :cCreatedAt="comment.createdAt"
          :cUserId="comment.userId"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import { fb } from "@/firebase";
import axios from "axios";
import { db } from "@/main";

import Comment from "@/components/Comment";
import OwnerPost from "@/components/OwnerPost";

export default {
  name: "Post",
  components: {
    Comment,
    OwnerPost
  },
  props: {
    _id: String,
    page: String,
    content: String,
    image: String,
    video: String,
    share: String,
    status: String,
    views: Number,
    createdAt: Number
  },
  data() {
    return {
      loggIn: false,
      comments: []
    };
  },
  mounted() {},
  created() {
    this.loginCheck();
    this.getComments();
  },
  methods: {
    loginCheck() {
      fb.auth().onAuthStateChanged(user => {
        if (user) {
          this.loggIn = true;
        } else {
          this.loggIn = false;
        }
      });
    },
    async getComments() {
      await axios.get(`${db}/posts/${this._id}/comments`).then(response => {
        this.comments = response.data;
      });
    }
  },
  computed: {}
};
</script>

<style scoped>
.img-avatar {
  width: 3rem;
  height: 3rem;
}

.img-avatar-boton {
  width: 2rem;
  height: 2rem;
}
</style>
